import React from 'react';

import Link from 'next/link'
import { Col } from "antd";

export default class FooterElement extends React.Component {
    convert =(string) => string.split(" ").join("-").toLowerCase();
    render(){
        let lists = this.props.children.map((item, i) => {
        return (
            <Link 
            key={i} 
            href={this.convert(this.props.page)+'/'+this.convert(item)}>
                <a>
                    <li style={{color:'white'}}>{item}</li>
                </a>
            </Link>
        )})
        return(
            <Col span={24} flex>
                <Link href={'/'+this.convert(this.props.page)}>
                    <a>
                        <h3 style={{color:'white', lineHeight:'1em'}}>{this.props.page}</h3>
                    </a>
                </Link>
                <h3>{this.props.page}</h3>
                <ul style={{listStyleType:'none', color:'white'}}>
                    {lists}
                </ul>
            </Col>
        )
    }
}


